.showcaseWpr {
  padding: 160px 0px 20px 0px;
  display: flex;
  justify-content: center;
}

.showcaseWpr img {
  width: 400px;
  height: 97px;
  margin-bottom: 16px;
}

@media only screen and (max-width: 768px) {
  .showcaseWpr {
    padding: 64px 0px;
  }

  .showcaseWpr img {
    width: 60%;
    margin-bottom: 0px;
  }
}
